.Diplomatic {
  /*  LAYOUT and main divisions */
  /* no changes here */
  /*  HEADER   */
  /*  fake selector bar */
  /* == FONT for the TEXT  !important! == */
  /* TEXT STYLING */ }
  .Diplomatic tei-projectDesc tei-list tei-item[n='dipl1'] {
    background: #ffdab9; }
  .Diplomatic tei-body * tei-p {
    font-family: Cardo;
    font-size: 1em; }
  .Diplomatic tei-pb {
    color: darkgrey;
    text-align: left;
    text-indent: -6em;
    line-height: 3.2em;
    font-size: 11px;
    float: left;
    font-family: "Courier New"; }
  .Diplomatic tei-pb::before {
    content: "Â " attr(n);
    padding: .7em .1em .1em .1em;
    border-top: 1px solid darkgrey;
    border-left: 1px solid darkgrey; }
  .Diplomatic tei-c[type=dropcap] {
    float: left; }
  .Diplomatic tei-choice tei-corr {
    display: none; }
  .Diplomatic tei-choice tei-sic {
    display: inline; }
  .Diplomatic tei-choice tei-orig {
    display: inline; }
  .Diplomatic tei-choice tei-reg {
    display: none; }
  .Diplomatic tei-supplied {
    display: none; }
  .Diplomatic tei-g {
    display: inline; }
  .Diplomatic tei-am {
    display: inline; }
  .Diplomatic tei-ex {
    display: none; }
  .Diplomatic tei-expan {
    display: none; }
  .Diplomatic tei-unclear {
    background-color: mistyrose; }
  .Diplomatic tei-lb {
    display: inline;
    color: black; }
  .Diplomatic tei-lb::after {
    content: '\a';
    white-space: pre; }
  .Diplomatic tei-lb[type="hyphen"]::before {
    content: " -";
