    letter-spacing: -.1em; }
  .Diplomatic tei-pb tei-facs {
    display: inline; }
  .Diplomatic tei-ref {
    color: #5f0000;
    text-decoration: underline;
    cursor: pointer; }
  .Diplomatic tei-seg {
    outline: 1px solid transparent; }

