/* HTML CSS */

.App {
  ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: #333;
  }
  
  li {
    float: left;
  }
  
  li a, li a:visited {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
    background-color: transparent;
    border-bottom: None;
  }
  
  li a:hover {
    background-color: #111;
  }

  li.active {
    background-color: #4CAF50;
  }
}